import React, { useEffect, useState } from 'react';

const sliderImages = [
  process.env.PUBLIC_URL + '/images/1000278926.jpg',
  process.env.PUBLIC_URL + '/images/1000278927.jpg',
  process.env.PUBLIC_URL + '/images/1000278928.jpg',
  process.env.PUBLIC_URL + '/images/1000278929.jpg',
];

const ProductList = ({ onAddToCart }) => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('products.json')
      .then(res => res.json())
      .then(data => {
        setProducts(data);
        setLoading(false);
      });
  }, []);

  if (loading) return <p>Loading products...</p>;

  // Only show Protein Powder
  const proteinPowder = products.find(p => p.title === 'Protein Powder');
  const proteinPowderImg = sliderImages[0];

  return (
    <div
      style={{
        height: '100vh',
        width: '100vw',
        overflow: 'hidden',
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-end',
        alignItems: 'center',
      }}
    >
      <img
        src={proteinPowderImg}
        alt={proteinPowder?.title || 'Protein Powder'}
        style={{
          width: '100vw',
          height: '100vh',
          objectFit: 'contain',
          objectPosition: 'center',
          position: 'absolute',
          top: 0,
          left: 0,
          zIndex: 1,
          background: '#000',
        }}
      />
      <div
        style={{
          position: 'relative',
          zIndex: 2,
          background: 'rgba(0,0,0,0.4)',
          color: '#fff',
          padding: '24px 16px',
          borderRadius: 16,
          marginBottom: 100,
          textAlign: 'center',
          width: '100%',
        }}
      >
        <h2 style={{ fontSize: 28, fontWeight: 700 }}>{proteinPowder?.title || 'Protein Powder'}</h2>
        <p style={{ fontSize: 22, fontWeight: 600 }}>â‚¹{proteinPowder?.price || 399}</p>
      </div>
      <button
        onClick={() => onAddToCart(proteinPowder)}
        style={{
          position: 'fixed',
          bottom: 32,
          left: '50%',
          transform: 'translateX(-50%)',
          background: 'linear-gradient(90deg, #007bff 60%, #00c6ff 100%)',
          color: '#fff',
          border: 'none',
          borderRadius: 24,
          padding: '18px 60px',
          fontSize: 22,
          fontWeight: 700,
          boxShadow: '0 2px 8px rgba(0,123,255,0.08)',
          cursor: 'pointer',
          zIndex: 10
        }}
      >
        Add to Cart
      </button>
      <style>{`
        html, body, #root {
          height: 100%;
          margin: 0;
          padding: 0;
        }
        @media (max-width: 600px) {
          img {
            object-fit: contain !important;
            object-position: center !important;
            width: 100vw !important;
            height: 100vh !important;
            background: #000 !important;
          }
        }
      `}</style>
    </div>
  );
};

export default ProductList;